import React from 'react'
import {entries} from "reducer";
import {museumActions} from "action";
import {bindActionCreators} from "redux";
import {connect} from "react-redux";
import {Flex} from 'antd-mobile'

import './index.scss'

interface P {
    className?: string,
    match: any,
    getMuseumDetail: any,
    museum: any
}

interface S {
}


class MuseumContact extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                museum
            },
            state: {}
        } = this
        return (
            <div className={`museum_contact_page`}>
                <Flex className={`bd_d`}>
                    <div className={'label'}>联系人</div>
                    <Flex.Item>{museum.linkman}</Flex.Item>
                </Flex>
                <Flex className={`bd_d`}>
                    <div className={'label'}>联系电话1</div>
                    <Flex.Item>{museum.phone}</Flex.Item>
                    <img src="/lsc_h5_hash/imgs/mobile.png" alt="" />
                </Flex>
                <Flex className={`bd_d`}>
                    <div className={'label'}>联系电话2</div>
                    <Flex.Item>{museum.phone2}</Flex.Item>
                    <img src="/lsc_h5_hash/imgs/mobile.png" alt="" />
                </Flex>
                <Flex className={`bd_d`}>
                    <div className={'label'}>微信</div>
                    <Flex.Item>{museum.wechat}</Flex.Item>
                    <img src="/lsc_h5_hash/imgs/copyBlue@3x.png" alt="" />
                </Flex>
            </div>
        );
    }

    getDetail = async () => {
        const {match: {params: {id}}, getMuseumDetail} = this.props
        const museum = await getMuseumDetail(id)
        document.title = '联系方式'
    }

    componentDidMount(): void {
        this.getDetail()
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}


function mapStateToProps(state: entries) {
    const {museum} = state.museumReducer
    return {
        museum
    }
}

function mapDispatchToProps(dispatch: any) {
    const {getMuseumDetail} = museumActions
    return {
        ...bindActionCreators({getMuseumDetail}, dispatch),
        dispatch
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(MuseumContact)
